function guideSlider() {
    var swiper = new Swiper('.guide-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        direction: 'vertical',
        speed: 1000,
        // watchOverflow: true,
        mousewheel: {
            releaseOnEdges: true,
        },
        pagination: {
            el: '.guide-slider .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        breakpoints: {
            320: {
                direction: 'horizontal',
                mousewheel: false,
            },
            992: {
                direction: 'vertical',
                mousewheel: {
                    releaseOnEdges: true,
                },
            },
        }
    })
}

guideSlider()
